.ui-album {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;

    .error-message {
        color: red;
    }

    .post-list {
        margin-top: 20px;

        .post {
            background-color: #f9f9f9;
            border-radius: 5px;
            padding: 20px;
            margin-bottom: 20px;


            .post-title {
                font-size: 20px;
                font-weight: bold;
                margin-bottom: 10px;
            }

            .post-body {
                font-size: 16px;
                color: #333;
            }
        }
    }
}